@import url(https://fonts.googleapis.com/css?family=Chakra+Petch);
@import url(https://fonts.googleapis.com/css?family=Chakra+Petch);
@import url(https://fonts.googleapis.com/css?family=Chakra+Petch);
@import url(https://fonts.googleapis.com/css?family=Chakra+Petch);
*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: #24292E; }

body {
  margin: 0;
  line-height: 1.6;
  font-family: "Chakra Petch", sans-serif;
  color: #6A737D; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

img {
  max-width: 100%;
  width: 100%;
  display: block; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

p {
  margin: 0; }

.l-container {
  max-width: 1200px;
  margin: 0 auto; }

.nav {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center; }
  .nav .menu {
    display: grid;
    height: 50%;
    width: 100%; }
    .nav .menu__item {
      padding: 0 16px;
      align-self: center;
      justify-self: center;
      background: #24292E; }
    .nav .menu__link {
      font-size: 24px;
      color: #ffffff; }

.dashboard {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  height: calc(100vh - 93px); }
  .dashboard .content {
    display: flex;
    justify-content: center;
    align-items: center; }
    .dashboard .content__logo {
      display: flex;
      justify-content: center; }
    .dashboard .content__text {
      font-size: 48px; }

.app {
  background: #ffffff; }
  .app .header {
    background: #24292E;
    text-align: center; }

.title {
  color: #24292E;
  border-bottom: 1px solid #E1E4E8;
  padding: 8px;
  font-size: 48px; }
  .title--light {
    color: #ffffff; }

